import EventEmitter from "@/util/EventEmitter";
import { defineComponent } from "vue";

export default defineComponent({
  name: "TagSettings",
  props: {
    settings: Object,
    name: String,
  },
  data() {
    return {
      x: 0,
      y: 0,
      decimals: 0,
      show: false,
      blockId: "",
      id: "",
      tagData: {} as any,
      tagElement: {} as any,
      tagType: String, // TODO: Type
    };
  },
  methods: {
    emitValue() {
      this.tagData["decimals"] = this.decimals;
      this.tagData["type"] = this.tagType;
      EventEmitter.emitter.emit(`tag-property-settings-${this.id}`, this.tagElement, this.tagData);
    },
    cancel() {
      this.show = false;
    },
    save() {
      this.emitValue();
      this.show = false;
    },
  },
  created() {
    EventEmitter.emitter.on("tag-property-settings", (blockId: string, data: any) => {
      this.x = data.x;
      this.y = data.y;
      this.id = data.id;
      this.blockId = blockId;
      this.show = true;
      this.tagData = data.tag.data;
      this.tagElement = data.tag.tag;
      this.decimals = data.tag.data.decimals || "";
      this.tagType = data.tag.data.type;
    });
  },
});
